.wrapper {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background: #f3f4f7;
}

.lane {
  max-height: calc(100% - 10px);
  width: 275px;
  margin: 0px 10px 0px 10px;
  padding-bottom: 10px;
  display: inline-block;
  vertical-align: top;
}

.lane-body {
  height: calc(100% - 50px);
  width: 100%;
  border-radius: 5px;
}

.filtercontainer {
  height: 52px;
  width: 100%;
  background-color: white;
}

.lanecontainer {
  width: 100%;
  height: calc(100% - 52px);
  padding: 10px 10px 0px 10px;
  overflow: auto;
}

.lane-title {
  display: inline-block;
  width: 100%;
  height: 20px;
  font-size: 14px;
  cursor: text;
  color: #172b4d;
}

.lane-title:hover i {
  display: inline-block;
}

.logo {
  color: #fea025;
  margin-bottom: 0;
}

.display-none {
  display: none;
}

.main-btn {
  background: #ffa000;
  font-size: 14px;
  border: none;
  border-radius: 5px;
  padding: 7px 10px 7px 10px;
  color: white;
  transition: 0.3s ease-in-out;
}

.taskcard {
  padding: 10px 15px;
  background: white;
  /* margin: 10px; */
  margin-bottom: 5px;
  border: 1px solid #ebecf0;
  border-radius: 5px;
  cursor: pointer;
}

.taskcard:hover {
  background: #fff9ea !important;
  border: 1px solid #ffe1bb;
}

.taskcard .title {
  color: #42526e;
  margin-bottom: 5px;
  font-weight: 500;
  font-size: 14px;
}

.pill {
  padding: 1px 5px 1px 5px;
  font-size: 13px;
  display: inline-block;
  border-radius: 10px;
  min-width: 60px;
  text-align: center;
  margin-bottom: 5px;
  font-weight: 400;
}

/* overiding bootstrap */

.float-right {
  float: right;
}
